@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;900&display=swap');

:root{
  --cw: #FDFDFD;
  --cr: #FF3847;
  --cp: #F97D87;
  --cb: #0E1A2A;
  --fp: 'Rubik', sans-serif;
}

*{
  box-sizing: border-box;
}
html{
  font: 400 10px / 1.5 var(--fp);
}
body {
  background: var(--cw);
  color: var(--cb);
  margin: 0;
  font-size: 1.8rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}