@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;900&display=swap);
:root{
  --cw: #FDFDFD;
  --cr: #FF3847;
  --cp: #F97D87;
  --cb: #0E1A2A;
  --fp: 'Rubik', sans-serif;
}

*{
  box-sizing: border-box;
}
html{
  font: 400 10px / 1.5 'Rubik', sans-serif;
  font: 400 10px / 1.5 var(--fp);
}
body {
  background: #FDFDFD;
  background: var(--cw);
  color: #0E1A2A;
  color: var(--cb);
  margin: 0;
  font-size: 1.8rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container{
    display: block;
    width: 100%;
    max-width: 60rem;
    margin: auto;
    padding: 0 2rem;
    position: relative;
}

.listReset{
    margin: 0;
    padding: 0;
    list-style: none;
}

.fx{ display: flex; }
.fx.as{ align-items: flex-start; }
.fx.ac{ align-items: center; }
.fx.ae{ align-items: flex-end; }
.fx.js{ justify-content: flex-start; }
.fx.jc{ justify-content: center; }
.fx.je{ justify-content: flex-end; }
.fx.ja{ justify-content: space-around; }
.fx.jb{ justify-content: space-between; }

.titles{
    font-size: 4.8rem;
    font-weight: 900;
    line-height: 1;
    text-transform: uppercase;
}
.titles::after{
    content: '_';
}

.sect_row{
    height: auto;
    min-height: 100vh;
}
.sect_col{
    padding: 5rem 0;
    position: relative;
    z-index: 2;
}

.deco{
    fill: var(--cp);
    display: block;
    position: absolute;
    z-index: 1;
    opacity: 0.9;
    pointer-events: none;
}
.deco use{
    transform: rotate(90deg);
    transform-origin: 50% 50%;
}

.header{
    display: block;
    width: 100%;
    margin: 2rem 0;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;
}
.header_logo{
    display: block;
    transition: transform 0.4s;
}
.header_logo:hover{
    transform: scale(1.1);
}
.header_hamb{
    display: block;
    margin: 0;
    padding: 0;
    border: 0;
    background: none;
    transition: transform 0.4s;
    cursor: pointer;
    outline: 0;
}
.header_hamb:hover{
    transform: rotate(15deg);
}
.header_hamb svg{
    display: block;
}
.hamb_poly{
    fill: var(--cr);
}
.hamb_line{
    stroke: var(--cw);
    stroke-width: 4px;
    transition: stroke-dashoffset 1s, stroke-dasharray 1s;
}
.hamb_top{
    stroke-dasharray: 40 160;
    transition-delay: 0.1s;
}
.hamb_mid{
    stroke-dasharray: 40 80;
}
.hamb_bot{
    stroke-dasharray: 40 220;
    transition-delay: 0.2s;
}
.header_hamb.active .hamb_top{
    stroke-dashoffset: -153;
}
.header_hamb.active .hamb_mid{
    stroke-dasharray: 0 80;
    stroke-dashoffset: -21;
}
.header_hamb.active .hamb_bot{
    stroke-dashoffset: -216;
}

.intro{
    display: block;
    position: relative;
}
.intro::before{
    background: var(--cr);
    display: block;
    width: 50%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    content: '';
}
.intro_row{
    position: relative;
    z-index: 2;
}
.intro_title{
    width: 100%;
    max-width: 2.95em;
    margin: auto;
    font-size: 10rem;
    line-height: 0.8;
    position: relative;
    z-index: 2;
}
.intro_title::after{
    display: none;
}
.intro_title span{
    display: block;
    margin: 1rem 0;
    padding: 0 0.4em;
    font-size: 0.14em;
    font-weight: 400;
    line-height: 1;
    letter-spacing: 0.22em;
    text-align: right;
}
.intro_title .hex{
    fill: var(--cw);
    width: 1.55em;
    height: auto;
    position: absolute;
    top: -0.4em;
    left: -0.4em;
    z-index: -1;
}
.intro_scroll{
    display: block;
    position: absolute;
    bottom: 5rem;
    left: calc(50% - 2rem);
    z-index: 2;
    transition: transform 0.4s;
}
.intro_scroll:hover{
    transform: scale(1.1);
}
.intro_scroll .hex{
    fill: var(--cb);
}
.intro_scroll .tri{
    fill: var(--cw);
}
.intro_deco1{
    top: 53%;
    right: 20%;
    z-index: 3;
}
.intro_deco2{
    bottom: 10%;
    left: 10%;
    z-index: 1;
}

.about{
    background: var(--cb);
    color: var(--cw);
}
.about_title{
    color: var(--cr);
}
.about_title::after{
    color: var(--cw);
}

.about{
    background: var(--cb);
    color: var(--cw);
}
.about_title{
    color: var(--cr);
}
.about_title::after{
    color: var(--cw);
}
.nums{
    display: block;
    margin: 5rem 0;
    text-align: center;
}
.num_item{
    color: var(--cr);
    display: inline-block;
    width: 50%;
    font-size: 6.4rem;
    font-weight: 900;
    line-height: 0.8;
}
.num_item span{
    color: var(--cw);
    display: block;
    font-size: 0.24em;
    text-transform: uppercase;
    transform: translateY(-0.6em);
}

.tech{
    display: block;
    margin: 5rem 0;
}
.tech_icon{
    display: block;
    width: 4rem;
    height: 4rem;
    font-size: 1.8rem;
    line-height: 2.4;
    text-align: center;
    position: relative;
    z-index: 1;
}
.tech_icon .hex{
    fill: var(--cr);
    display: block;
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}
.about_deco1{
    top: 30%;
    right: 0;
}
.about_deco2{
    bottom: 20%;
    left: 5%;
    z-index: 3;
}

.contact{
    background: var(--cr);
    color: var(--cw);
}
.contact_title{
    color: var(--cb);
}
.contact_title::after{
    color: var(--cw);
}

.social{
    display: block;
    margin: 3rem 0;
}
.social_item{
    display: block;
    margin-right: 3rem;
}
.social_link{
    color: var(--cb);
    display: block;
    width: 4rem;
    height: 4rem;
    font-size: 1.8rem;
    line-height: 2.4;
    text-align: center;
    position: relative;
    z-index: 1;
}
.social_link .hex{
    fill: var(--cw);
    display: block;
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: transform 0.4s;
}
.social_link:hover{
    color: var(--cr);
}
.social_link:hover .hex{
    transform: scale(1.2) rotate(30deg);
}
.contact_deco1{
    top: 45%;
    right: 2%;
}
.contact_deco2{
    bottom: 40%;
    left: 7%;
}
.footer{
    background: var(--cr);
    color: var(--cw);
    display: block;
    padding: 1em 0;
    font-size: 1rem;
    line-height: 1;
    letter-spacing: 0.1em;
    text-align: center;
    text-transform: uppercase;
}
.footer .fa-heart{
    font-size: 1.2em;
    color: var(--cb);
}
.footer strong{
    color: var(--cb);
    font-weight: 900;
}
.co{
    background: #003893;
    width: 2rem;
    height: 1rem;
    margin: 0 0.3rem;
    font-size: 0;
    display: inline-flex;
    justify-content: space-between;
    box-shadow: 0 0 0 1px var(--cb);
}
.co::before,
.co::after{
    display: block;
    height: 1rem;
    content: '';
}
.co::before{
    background: #FCD116;
    width: 40%;
}
.co::after{
    background: #CE1126;
    width: 30%;
}

@media screen and (min-width: 767px) {
    .titles{
        font-size: 6.4rem;
    }
    .intro_title{
        font-size: 13rem;
    }
    .num_item{
        font-size: 13rem;
    }
    .tech_icon{
        width: 6rem;
        height: 6rem;
        font-size: 2.4rem;
        line-height: 2.7;
    }
}
